import axios from 'axios';

const api = {
    product : '/api/test/query2',
};

function fetchProduct(){
    return axios.get(api.product);
}

function fetchAsk(){
    return axios.get(api.ask);
}

function fetchJobs(){
    return axios.get(api.jobs);
}
//axios   api    views    
function fetchUser(){
    return axios.get(api.user);
}
function fetchItem(){
    return axios.get(api.item);
}

export {
    fetchProduct,
    fetchAsk,
    fetchJobs,
    fetchUser,
    fetchItem
}